import React, { Component } from "react";

import jQuery from 'jquery'; 
import Fade from "react-reveal";


import Grid from '@material-ui/core/Grid';










const divStyle = {
  color: '#8e6115',
background: '#8e6115',
};



  
  
const colorprogess = {
  color: '#8e6115',
background: '#8e6115',
};



const letrasprogress = {
  color: '#adbfc4',

};





class Habilidades extends Component {








  
  render() {
    if (!this.props.data) return null;


    const skill = this.props.data.skill.map(function (skill) {
    
      return (
    
      
   
<div className="container-skillbar">
<div className="skillbar clearfix " data-percent={skill.porcentaje}>
<div className="skillbar-title" style={colorprogess}><span style={letrasprogress}>{skill.titulo}</span></div>
<div className="skillbar-bar" style={divStyle}></div>
<div className="skill-bar-percent">{skill.porcentaje}</div>
</div> 
</div>

      );
    });






    const skill2 = this.props.data.skill2.map(function (skill2) {
  
 
      return (
    
      
   
<div className="container-skillbar">
<div className="skillbar clearfix " data-percent={skill2.porcentaje}>
<div className="skillbar-title" style={colorprogess}><span style={letrasprogress}>{skill2.titulo}</span></div>
<div className="skillbar-bar" style={divStyle}></div>
<div className="skill-bar-percent">{skill2.porcentaje}</div>
</div> 
</div>

   
      );
    });


 
    jQuery(document).ready(function(){
      jQuery('.skillbar').each(function(){
        jQuery(this).find('.skillbar-bar').animate({
          width:jQuery(this).attr('data-percent')
        },3000);
      });
    });



 
    return (


     


      <section id="habilidades">
        <Fade left duration={1000} distance="40px">
<center>
<h1 className="tituloH">Habilidades</h1></center>
<br></br>

        <Grid container spacing={1} className="progress1">
 
 <Grid item xs={12} sm={6} >
            
            
                {skill}
         
</Grid>

<Grid item xs={12} sm={6} >
            
            
            {skill2}
     
     
</Grid>
</Grid>
        </Fade>
      </section>
    );
  }
  
}

export default Habilidades;
