import React, { Component } from "react";
import { Fade, Slide } from "react-reveal";
import emailjs from 'emailjs-com';
import $ from 'jquery'

class Contacto extends Component {
  render() {
    if (!this.props.data) return null;

    const nombre = this.props.data.nombre;
    const pais = this.props.data.address.pais;
    const ciudad = this.props.data.address.ciudad;
    const telefono = this.props.data.telefono;
    const email = this.props.data.email;

   
     
    function sendEmail(e) {
      e.preventDefault();
  
      emailjs.sendForm('contact_form', 'template_t2rpjvc', e.target, 'user_S54KJCC5AcgLVVCABxuTI')
        .then((result) => {
            console.log(result.text);
          mostrar();
        }, (error) => {
            console.log(error.text);
           
        });

    

      function mostrar() {
          var x = document.getElementById('mi-super-contenido');
          if (x.style.display === 'none') {
              x.style.display = 'none';
          } else {
            
              x.style.display = 'block';
              $('#reset').click();
             
          }
      }

      }
    return (
      
      <section id="contacto">
        <Fade bottom duration={1000}>
       
        </Fade>
<br></br>
        <div className="row">
          <Slide left duration={1000}>
            <div className="Email"><br></br>
            <form action="" method="post" id="contactoForm" name="contactoForm" onSubmit={sendEmail}>
      <input type="hidden" name="contact_number" />
      <label>Nombre</label>  <br />
      <input type="text" name="user_name" />  <br />
      <label>Email</label>  <br />
      <input type="email" name="user_email" />  <br />
      <label>Mensaje</label>  <br />
      <textarea name="message" />  <br />
      <input type="submit" value="Enviar"  className="button"/>
      <input id="reset" type="reset" value="Limpiar (:"/>
      <div id="mi-super-contenido">Mensaje enviado con exito!</div>
    </form>

            
            </div>
          </Slide>

          <Slide right duration={1000}>
            <center>
            <aside className="four footer-widgets">
              <div className="widget widget_contact">
              <br />
                <h4>Contacto</h4>
                
                <p className="direccion">
                  {nombre}
                  <br />
                  {pais}
                  {ciudad}
                  <br />
                  {email}
                  <br />                  <span>{telefono}</span>
                </p>
              </div>

        
            </aside></center>
          </Slide>
        </div>
      </section>
    );
  }
}

export default Contacto;
