import React, { Component } from "react";
import Fade from "react-reveal";
import LinkedInIcon from '@material-ui/icons/LinkedIn';

import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import GitHubIcon from '@material-ui/icons/GitHub';




class Header extends Component {



  
  render() {
    if (!this.props.data) return null;

    const github = this.props.data.github;
    const linkedin = this.props.data.linkedin;
    const nombre = this.props.data.nombre;
    const descripcion = this.props.data.descripcion;

    return (
      <header id="inicio">
 

    


    
     



        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="  Abrir Menu">
           Abrir Menu
          </a>
          <a className="mobile-btn" href="#inicio" title=" Cerrar Menu">
            Cerrar Menu
          </a>

          <ul id="nav" className="nav">
            <li className="current">
              <a className="smoothscroll" href="#inicio">
                Inicio
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#sobreMi">
                Sobre mi
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#habilidades">
                habilidades
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#portfolio">
                Trabajos
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#contacto">
                Contacto
              </a>
            </li>
          </ul>
        </nav>
        <div className="row banner">
          <div className="banner-text">
            <Fade bottom>

              
              <h1 className="responsive-headline"> <br></br> <br></br>{nombre}</h1>
            </Fade>
            <Fade bottom duration={1200}>
            

<br></br>   <h3 >{descripcion}</h3>  <br></br>   </Fade>
            <hr /> <br></br>
            <Fade bottom duration={2000}>
              <ul className="social">
                <a href={github} className="button btn project-btn">
                  <GitHubIcon/> Github
                </a>
                <a href={linkedin} className="button btn linkedin-btn">
                  <LinkedInIcon/>  Linkedin
                </a>
              </ul>
            </Fade>
          </div>
          <br></br>
          <br></br> 
          <div id="go-back">
            <center>
            <a className="smoothscroll"  href="#sobreMi">
              
              <i className="icon-up-open"><ArrowDropDownIcon/></i>
            </a></center>
          </div>
        </div>

        
      </header>
    );
  }
}

export default Header;
