import React, { Component } from "react";
import ReactGA from "react-ga";
import $ from "jquery";
import "./App.css";

import Contact from "./component/Contacto";
import SobreMi from "./component/SobreMi";
import Header from "./component/Header";
import Footer from "./component/Footer";
import Habilidades from "./component/Habilidades";
import Portfolio from "./component/Portfolio";



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foo: "bar",
      Datos: {}
    };

   ReactGA.initialize("G-Q7HR9384VQ");
    ReactGA.pageview(window.location.pathname);
  }

  getDatos() {
    $.ajax({
      url: "./Datos.json",
      dataType: "json",
      cache: false,
      success: function(data) {
        this.setState({ Datos: data });
      }.bind(this),
      error: function(xhr, status, err) {
        console.log(err);
        alert(err);
      }
    });
  }

  componentDidMount() {
    this.getDatos();
  }

  render() {
    return (
      <div className="App">
       
       <Header data={this.state.Datos.main} />
        <SobreMi data={this.state.Datos.main} />
        <Habilidades data={this.state.Datos.habilidades}/>
        <Portfolio data={this.state.Datos.portfolio} />
        <Contact data={this.state.Datos.main} />
        <Footer data={this.state.Datos.main} />
  
      </div>
    );
  }
}

export default App;