 
 import React, { Component  } from "react";
 import Fade from "react-reveal";
 
 
 
 
 let id = 0;
 class Portfolio extends Component {
 
 
   render() {
   
     if (!this.props.data) return null;
   
     const proyectos = this.props.data.proyectos.map(function (proyectos) {
       let proyectosimagen = "imagenes/portfolio/" + proyectos.imagen;
      
 
       return (
        
 
         <div key={id++}>
       <div className="card123">
       <div class="feature-card" style={{ backgroundImage: `url(${proyectosimagen})` }}>
		<div class="feature-card-tilte">
			<h3>
				{proyectos.titulo}
			</h3>
		</div>
		<div class="feature-card-deatails">
			<i class="fa fa-bank"></i>
			<h4>	{proyectos.titulo}</h4>
			<p>	{proyectos.category}</p>
			<a href={proyectos.url}>	{proyectos.link}</a>
		</div>
	</div>
         </div>
         </div>
       );
     });
 
 
 
 
 
 
 
 
 
 
 
 
     return (
       <section id="portfolio">
       <Fade left duration={1000} distance="40px">
        <br></br>
         
             <h1>Algunos de mis trabajos</h1>
 
           
                 {proyectos}
                 
 
                 <br></br>
         
         </Fade>
       </section>
     );
   }
 }
 
 export default Portfolio;
 
        