import React, { Component } from "react";
import Fade from "react-reveal";
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import descargarCV from './cv/CV Guerin.pdf'


class SobreMi extends Component {
  render() {
    if (!this.props.data) return null;

    const nombre = this.props.data.nombre;
    const imagen = "imagenes/" + this.props.data.imagen;
    const bio = this.props.data.bio;
    const pais = this.props.data.address.pais;
    const ciudad = this.props.data.address.ciudad;
    const telefono = this.props.data.telefono;
    const email = this.props.data.email;

    return (
      <section id="sobreMi">
        <Fade duration={1000}>
          <div className="row">
          <div className="three columns">
          <img
                className="imagen"
                src={imagen}
                alt="imagen de perfil Ale Guerin"
              />
            </div>
          
            <div className="nine columns main-col">
              <h2>Sobre mi </h2>

              <p className="bioo">{bio}</p>



              <div className="row">
            
                <div className="columns contacto-details">
               
                  <h2>Contacto</h2>
                  <p className="address">
                    <span>{nombre}</span>
                    <br />
                    <span>
                      {pais}
                     
                      {ciudad} 
                    </span>
                    <br />
                    <span>{telefono}</span>
                    <br />
                    <span>{email}</span>
                  </p>
                </div>

              
                <div className="columns descargas">
               
                  <p>
                    <a href={descargarCV} className="button">
                      <CloudDownloadIcon/>  Descarga mi CV
                    </a>
                  </p>
                </div>
              </div>
            </div>
            </div>
       
        </Fade>
        
      </section>
    );
  }
}

export default SobreMi;
